@import "_variables";

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  margin: 0;
  // Provide a default background for when some browsers "scroll off the page"
  // as part of their animation when the top or bottom is reached.
  background-color: black;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: $nav-width;

  @include setup(
    $backgroundColor: map-get($colors, "black"),
    $backgroundImage: "/images/tile_blk.gif",
    $linkActiveColor: map-get($colors, "electricViolet1"),
    $linkColor: map-get($colors, "brightRed2"),
    $linkVisitedColor: map-get($colors, "blazeOrange"),
  );

  .media-and-resources {
    padding-top: 0.5rem;
    $gold: map-get($colors, "socialMediaGold");

    a {
      border: 1px solid $gold;
      color: $gold;
      display: flex;
      font-family: "EB Garamond";
      font-size: 17px;
      font-weight: 500;
      line-height: 0.75;
      padding: 4px 0 7px;
      text-align: center;
      justify-content: center;
      text-decoration: none;
    }

    &:hover, &.active {
      a {
        $silver: rgb(190, 190, 190);
        border-color: $silver;
        color: $silver;
      }
    }
  }
}

@font-face {
  font-family: "EB Garamond";
  src: url("/fonts/EBGaramond-Regular.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: "EB Garamond";
  src: url("/fonts/EBGaramond-Medium.ttf") format("truetype");
  font-weight: 500;
}

.root {
  margin-left: $nav-width;
  flex-grow: 1;

  .header {
    position: fixed;
    top: 0;
    right: 0;
    left: $nav-width;
    height: $header-height;
    padding: 4px;
    z-index: 1;
  }

  .main {
    min-height: 100%;
    padding: $main-padding;
    padding-top: $header-height + $main-padding;
  }
}
