$colors: (
  "anakiwa1": #80FFFF,
  "anakiwa2": #9CCEFF,
  "anakiwa3": #A6FFFF,
  "black": #000000,
  "blazeOrange": #FF6300,
  "blue": #0000FF,
  "brightRed1": #A9000E,
  "brightRed2": #AD0000,
  "castro": #520029,
  "ceruleanBlue": #315ACE,
  "cornflowerBlue": #8D6BF5,
  "cyanAqua": #00FFFF,
  "deepFir": #002900,
  "deepTeal": #002D38,
  "electricViolet1": #8000FF,
  "electricViolet2": #D304FF,
  "flamenco": #FF7706,
  "gold": #FFD100,
  "green": #00FF00,
  "guardsmanRed": #BD0000,
  "ivory": #FFFFEF,
  "lightningYellow": #FFC123,
  "magentaFuchsia": #FF00FF,
  "navyBlue1": #000863,
  "navyBlue2": #120080,
  "rajah": #F5C478,
  "red": #F20600,
  "stratos1": #000329,
  "stratos2": #010648,
  "toreaBay": #160FAD,
  "white": #FFFFFF,
  "socialMediaGold": rgb(220, 200, 130),
);

$nav-width: 126px;
$header-height: 100px;
$main-padding: 10px;

@mixin setup(
  $backgroundColor: null,
  $backgroundImage: null,
  $linkColor: null,
  $linkActiveColor: null,
  $linkVisitedColor: null,
) {
  @if $backgroundColor {
    background-color: $backgroundColor;
  }
  @if $backgroundImage {
    background-image: url($backgroundImage);
  }
  @if $linkColor {
    a {
      color: $linkColor;
    }
  }
  @if $linkActiveColor {
    a:active {
      color: $linkActiveColor;
    }
  }
  @if $linkVisitedColor {
    a:visited {
      color: $linkVisitedColor;
    }
  }
}
